body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
}

section {
    display: block;
}
        
div {
    display: block;
}

.graph {
    display: block;
}

.container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row wrap;
        flex-flow: row wrap;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    text-align: center;
}

.chart {
    width: 100%; 
    min-height: 250px;
}

#loadingGif {
    width: 100%;
}

#header {
    width: 100%;
    height: 70px;
    background-color: #FFF;
    border-bottom: solid #EEE 1px;
    z-index: 3;
    font-family: "Round", Verdana, Tahoma;
}
        
#header-section {
    width: 80%;
    margin: 0 auto;
    margin-top: 0px;
    margin-right: auto;
    margin-bottom: 0px;
    margin-left: auto;
}
        
.hexa-div {
    width: 15%;
    float: left;
    text-align: center;
    line-height: 100%;
}
        
#header-section a {
    font-size: 1.1em;
    line-height: 70px;
}
        
.black {
    background: url(https://goverlytics.com/img/Black-Burried-Pattern-for-Websites.jpg);
    color: white;
}

.officials {
    height: 200px;
    width: 200px;
    display: block;
    margin-left: auto;
    margin-right: auto;
}

.officialsNames {
    text-align: center;
    vertical-align: middle; 
}

.chartWithOverlay {
    width: 100%; 
    min-height: 250px;
    position: relative;
}
.overlay {
    width: 20%;
    min-height: 250px;
    position: absolute;
    top: 2%;  
    left: 80%; 
}

.check {
    max-width:50%
}

.icon {
    width: 10%;
    min-height: 25px;
}

.score-row {
    text-align: center;
    min-height: 50px;
}

.current {
    font-weight: bold;
}

.card {
    display: inline-block;
}



